.list {
  color: #fff;
  width: 320px;
  background-color: #5e584c;
  height: 50px;
  margin: 20px auto;
  border-radius: 5px;
}
.list p {
  padding: 10px;
  position: relative;
}
.list p span {
  position: absolute;
  right: 10px;
  cursor: pointer;
}

.list p input {
  background-color: transparent;
  border: 0px;
  color: #fff;
  font-size: 20px;

}
.list p input:focus {
  outline: none;
}

#checkbox {
  position: relative;
  top: 3px;
  height: 18px;
  width: 18px;
}
#checkbox [type=checkbox]:checked {
  background-color: #816630 !important;
  color: #ffffff !important;
}