body {
  background-image: url("../src/coffee1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.App {
  background-color: rgb(255, 255, 255, 0.8);
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  border-radius: 20px;
}
#to-do-form input {
  background-color: rgb(143, 98, 47);
  border: 0px;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #ffff;
}
#to-do-form input::placeholder {
  color: rgb(255, 255, 255, 0.5);
}
#to-do-form input:focus,
#to-do-form button:focus {
  outline: none;
}
#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0px;
  background-color: rgb(99, 87, 56);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}
